
import {defineComponent} from 'vue'
import {markdownToHtml} from '@/lib/markdown'

export const MarkdownView = defineComponent({
  name: 'MarkdownView',
  props: {
    tag: {
      type: String,
      default: 'div',
    },
    markdown: {
      type: String,
      required: true,
    },
  },
  computed: {
    html(): string {
      return markdownToHtml(this.markdown)
    },
  },
})

export default MarkdownView
